.footer {
  position: fixed;
  bottom: 0;
  width: 100vw;
  height: 45px;
  background-color: #262626;
  border: #707070;
  ul {
    display: flex;
    width: 100%;
    height: 100%;
    align-items: center;
    justify-content: center;
    li {
      list-style-type: none;
      a {
        display: block;
        position: relative;
        width: 24px;
        height: 24px;
        margin-left: 15px;
        margin-right: 15px;
      }
    }
  }

  .noti-count {
    position: absolute;
    right: 3px;
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background-color: #db2a2a;
    font-size: 7px;
    text-align: center;
    text-decoration: none;
    color: #ffffff;
  }
}
