.overlay {
    position: fixed;
    z-index: 1;
    top: 0; left: 0; right: 0; bottom: 0;
    user-select: none;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgba(0, 0, 0, 0.8);

    .order {
        background-color: #fff;
        color: black;
        width: 90vw;
        border-radius: 8px;
        .food {
            display: flex;
            
            img {
                width: 40%;
            }
            .food-info {
                padding: 8px 8px;
                display: flex;
                flex-flow: column;
                justify-content: space-between;

                .name {
                    font-weight: bold;
                }
                textarea {
                    resize:none;
                }
            }
        }
        .order-button {
            color: white;
            text-align: center;
            background-color: #1AB3EB;
            border-bottom-left-radius: 8px;
            border-bottom-right-radius: 8px;
            font-weight: bold;
            font-size: 1.2rem;
            padding: 16px;
        }
    }
}