@font-face {
  font-family: "LunchType";
  src: url('./assets/fonts/Lunchtype_21-Regular.ttf') format("ttf");
}
@font-face {
  font-family: "LunchType";
  src: url('./assets/fonts/Lunchtype_21-Bold.ttf') format("ttf");
  font-weight: bold;
}

body {
  margin: 0;
  font-family: "LunchType";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #000;
  color: #fff;
}


* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
