.header {
    display: flex;
    flex-flow: row;
    justify-content: space-between;
    align-items: center;
    padding: 8px 10px;
    margin-bottom: 8px;

    .user {
        display: flex;
        align-items: center;
        font-size: 0.9rem;
        img {
            max-width: 35px;
            border-radius: 50%;
        }
        .hello {
            padding: 0px 8px;
        }
    }

    .gameloft-villiage {        
        img {
            max-width: 120px;
        }
        span {
            font-size: 0.8rem;
            margin-top: 5px;
        }
    }
    
}