.home {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  .user-icon {
    width: 150px;
    height: 150px;
    margin-top: 120px;
    position: relative; 
    .avatar {
      width: 100%;
      border-radius: 50%;
    }
    .plus {
      position: absolute;
      right: 20px;
      bottom: 2px;
      cursor: pointer;
    }
  }
  .welcome {
    margin: 24px 0;
    text-align: center;
    p {
      font-size: 1.5rem;
      font-weight: bold;
    }
  }
  .messages {
    padding: 0 32px;
    text-align: center;
  }
  .btn-next {
    outline: none;
    font-weight: bold;
    color: #1AB3EB;
    text-align: center;
    text-decoration: none;
    border: 3px solid #1AB3EB;
    padding: 22px 100px;
    margin-top: 100px;
  }
}
