.login {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  .logo {
    background-image: url("../../assets/images/logo_login.png");
    background-size: contain;
    background-position: center;
    width: 130px;
    height: 130px;
    margin-top: 100px;
  }

  .gameloft-villiage {
      margin: 55px auto;
  }

  .input-text {
    height: 40px;
    width: 55vw;
    margin: 10px;
    background: #ffffffff;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0px;
    border-radius: 1px;

    input[type="text"],
    input[type="password"] {
      border: none;
      width: 100%;
      padding: 0px 15px;
    }
  }

  .btn-login {
    outline: none;
    font-weight: bold;
    color: #1AB3EB;
    text-align: center;
    text-decoration: none;
    border: 3px solid #1AB3EB;
    padding: 22px 100px;
    margin-top: 40px;
  }
}
