.app-list {
  margin: 25px 34px 0px 34px;
  background-color: #0f0f0f;
  font-size: 13px;
  padding-top: 22px;
  height: 50vh;
  padding: 22px 21px 0px 21px;
  ul {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    justify-items: center;
    li {
      list-style: none;
      a {
        display: flex;
        flex-direction: column;
        text-decoration: none;
        text-align: center;
        color: #ffffff;
        user-select: none;
        div {
          position: relative;
          margin: 8px auto 8px auto;
          width: 62px;
          height: 62px;
          border: 1px solid #28b2eb;
          border-radius: 50%;
          svg {
            margin-top: 17px;
            fill: #1ab3eb;
          }
        }
        &:hover > div {
          background-image: linear-gradient(130deg, #28b2eb, #5903a2);
        }
        &:hover svg {
          fill: #ffffff;
        }
      }
    }
  }
  .club-icon {
    svg {
      margin-top: 23px;
      fill: #1ab3eb;
    }
  }
}
